// zero-based
const f = (n: number) => n - 1

// noinspection JSUnusedGlobalSymbols
export const
  first = f(1),
  //second = f(2),
  third = f(3),
  fourth = f(4),
  fifth = f(5),
  sixth = f(6),
  seventh = f(7),
  //eighth = f(8),
  ninth = f(9),
  tenth = f(10),
  eleventh = f(11),
  twelfth = f(12),
  thirteenth = f(13)
