import {useEffect} from 'preact/hooks'

export function useFrame (f: () => void, inputs?: unknown[]) {
  useEffect(() => {
    let running = true
    const loop = () => {
      f()
      if (running) window.requestAnimationFrame(loop)
    }
    loop()
    return () => {
      running = false
    }
  }, inputs)
}
