export type Pitch = {
  letter: 'A' | 'B' | 'C' | 'D' | 'E' | 'F' | 'G'
  accidental?: 'bb' | 'b' | '' | '#' | '##'
  octave: number
}

export const accidentals = {
  'bb': -2,
  'b': -1,
  '': 0,
  '#': 1,
  '##': 2
}

export const pitchLetters = {
  C: 0,
  D: 2,
  E: 4,
  F: 5,
  G: 7,
  A: 9,
  B: 11
}

export function pitchFromString (str: string): Pitch {
  const pattern = /^([A-G])(b|bb|#|##)?([0-9])$/
  const match = str.match(pattern)
  if (!match) throw new Error(`could not parse pitch: ${str}`)
  const [, letter, accidental = '', octave] = match
  return {letter, accidental, octave: parseInt(octave)} as Pitch
}

export function midiPitchFromString (x: unknown): number {
  const str = String(x)
  const num = parseInt(str)
  if (!isNaN(num)) return num
  return midiPitchFromPitch(pitchFromString(str))
}

export function midiPitchFromPitch ({letter, octave, accidental = ''}: Pitch) {
  return pitchLetters[letter] + accidentals[accidental] + 12 * (octave + 1)
}

export function hertzFromMidiPitch (x: number) {
  return 440 * Math.pow(2, (x - 69) / 12)
}

export function midiPitchFromHertz (x: number) {
  return 12 * Math.log2(x / 440) + 69
}
