import {h, RenderableProps} from 'preact'
import {Dim, Pair, Scale} from '../utils'

export const Shape = ({
  children,
  onClick = () => {},
  geometry = [0, 0],
  scale = [x => x, x => x],
  axis = 0,
  interactive = false
}: RenderableProps<{
  scale?: Pair<Scale>
  axis?: 0 | 1
  geometry?: Pair<Dim>
  interactive?: boolean
  onClick?: () => void
}>) => {

  const toPair = (x: Dim): Pair => {
    const [a, b] = typeof x === 'number' ? [x, x] : x
    return [Math.min(a, b), Math.max(a, b)]
  }

  const swap = <T extends any> ([a, b]: Pair<T>, yes: boolean = true): Pair<T> => {
    return yes ? [b, a] : [a, b]
  }

  const [[x0, x1], [y0, y1]] = swap([
    toPair(geometry[0]),
    toPair(geometry[1])
  ], axis === 1)

  const pc = (n: number) => 100 * n + '%'

  return (
    <div
      class="shape"
      onClick={onClick}
      style={{
        pointerEvents: interactive ? 'initial' : 'none',
        position: 'absolute',
        left: pc(scale[0](x0, 1)),
        bottom: pc(scale[1](y0, 1)),
        width: pc(scale[0](x1 - x0, 0)),
        height: pc(scale[1](y1 - y0, 0))
      }}>
      {children}
    </div>
  )
}
