export type LabelProps = {
  side: 'top' | 'left' | 'bottom' | 'right',
  offset?: string
}

export function Label ({
  side,
  offset = '0'
}: LabelProps): Record<string, any> {
  const custom = {
    top: {
      left: '50%',
      bottom: '100%',
      marginBottom: offset,
      transform: 'translateX(-50%)'
    },
    bottom: {
      left: '50%',
      top: '100%',
      marginTop: offset,
      transform: 'translateX(-50%)'
    },
    left: {
      top: '50%',
      right: '100%',
      marginRight: offset,
      transform: 'translateY(-50%)'
    },
    right: {
      top: '50%',
      left: '100%',
      marginLeft: offset,
      transform: 'translateY(-50%)'
    }
  }
  return {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    position: 'absolute',
    whiteSpace: 'nowrap',
    ...custom[side]
  }
}
