import {Vis} from './components/Vis'
import {Geometry, Note} from './utils'
import {Fragment, h, VNode} from 'preact'
import {Exp, seq, Seq} from '@dipl/lib-core'

export function vis<Y extends Note, X> ({
  axiom,
  notes = [],
  lines = ys => ys,
  bound = ys => ys,
  input = () => <Fragment/>
}: {
  axiom: X
  notes?: Exp<Seq<Y>, X>
  lines?: (ys: Y[]) => Exp<Seq<Geometry>, X>
  bound?: (ys: Y[]) => Exp<Seq<Geometry>, X>
  input?: (axiom: X, setAxiom: (axiom: X) => void) => VNode
}) {
  return <Vis {...{
    axiom,
    input,
    notes: seq(notes),
    lines: (ys: Y[]) => seq(lines(ys)),
    bound: (ys: Y[]) => seq(bound(ys))
  }}/>
}
