import {useTheme} from './Theming'
import {Fragment, h} from 'preact'
import {useHover} from '../hooks/useHover'
import {useFocus} from '../hooks/useFocus'
import {useRef, useState} from 'preact/hooks'

export function InputField<T> (props: {
  value: T
  setValue: (value: T) => void
  type?: string
  step?: number
  min?: number
  max?: number
  ticks?: number[]
  minLength?: number
  parse: (str: string) => T
  stringify: (value: T) => string
}) {
  const ref = useRef<HTMLElement>()
  const theme = useTheme()
  const hover = useHover(ref)
  const focus = useFocus(ref)
  const [listId] = useState('datalist-' + String(Math.random()).substr(2))
  return (
    <Fragment>
      <input
        ref={ref as any}
        type={props.type}
        step={props.step || 'any'}
        min={props.min}
        max={props.max}
        minLength={props.minLength}
        list={props.ticks ? listId : undefined}
        value={props.stringify(props.value)}
        onChange={(e: any) => props.setValue(props.parse(e.target.value))}
        style={{
          width: '100%',
          color: focus ? theme.textPrimary : theme.textSecondary,
          background: hover || focus ? theme.backgroundHover : 'none',
          border: `1px solid ${theme.divider}`,
          borderRadius: theme.cardRadius,
          fontFamily: 'inherit',
          fontSize: 'inherit',
          padding: '0.25rem 0.5rem',
          outline: 'none'
        }}
      />
      {props.ticks && (
        <datalist id={listId}>
          {props.ticks.map(value => (
            <option value={value}/>
          ))}
        </datalist>
      )}
    </Fragment>
  )
}
