import {useTheme} from './Theming'
import {h} from 'preact'
import {useRef} from 'preact/hooks'
import {useHover} from '../hooks/useHover'
import {useFocus} from '../hooks/useFocus'

export function SelectField<T> (props: {
  value: T
  setValue: (value: T) => void
  items: T[]
  parse: (str: string) => T
  stringify: (value: T) => string
}) {
  const theme = useTheme()
  const ref = useRef<HTMLElement>()
  const hover = useHover(ref)
  const focus = useFocus(ref)
  return (
    <select
      ref={ref as any}
      value={props.stringify(props.value)}
      onChange={(e: any) => props.setValue(props.parse(e.target.value))}
      style={{
        width: '100%',
        fontFamily: 'inherit',
        fontSize: 'inherit',
        padding: '0.25rem 0.5rem',
        color: focus ? theme.textPrimary : theme.textSecondary,
        background: hover || focus ? theme.backgroundHover : 'none',
        border: `1px solid ${theme.divider}`,
        borderRadius: theme.cardRadius
      }}
    >
      {props.items.map(item => (
        <option value={props.stringify(item)}>
          {props.stringify(item)}
        </option>
      ))}
    </select>
  )
}
