import {ThemeToggle} from './ThemeToggle'
import {h, VNode} from 'preact'
import {useEffect, useState} from 'preact/hooks'
import {Theming} from './Theming'

const isNarrow = () => {
  return innerWidth < 768
}

export function Layout (props: {
  header: VNode
  aside: VNode
  main: VNode
}) {
  const [narrow, setNarrow] = useState(isNarrow())

  useEffect(() => {
    const handler = () => setNarrow(isNarrow())
    window.addEventListener('resize', handler)
    return () => {
      window.removeEventListener('resize', handler)
    }
  }, [])

  return (
    <Theming
      dark={true}
      render={({theme, dark, setDark}) => (
        <article style={narrow ? {
          display: 'flex',
          flexDirection: 'column-reverse'
        } : {
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          display: 'flex'
        }}>
          <aside style={{
            width: narrow ? '100%' : '320px',
            display: 'flex',
            flexDirection: 'column',
            borderRight: narrow ? '' : '1px solid ' + theme.divider
          }}>
            <header style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              flexDirection: 'row',
              borderBottom: '1px solid ' + theme.divider
            }}>
              {props.header}
              <ThemeToggle
                dark={dark}
                onChange={setDark}
              />
            </header>
            <div style={{
              display: 'flex',
              flexDirection: 'column',
              overflow: 'auto'
            }}>
              {props.aside}
            </div>
          </aside>
          <main style={narrow ? {
            width: '100%',
            height: 'calc(100vh - 64px)',
            position: 'relative',
            overflow: 'hidden',
            borderBottom: '1px solid ' + theme.divider
          } : {
            display: 'flex',
            flex: 1,
            position: 'relative'
          }}>
            {props.main}
          </main>
        </article>
      )}
    />
  )
}