export type Props = Readonly<{
  index: number
  min: number
  max: number
  alternate: boolean
  inclusive: boolean
  reverse: boolean
}>

const mod = (n: number, m: number): number => {
  return ((n % m) + m) % m
}

export default function osc ({
  index = 0,
  min = 0,
  max = 0,
  alternate = false,
  inclusive = false,
  reverse = false
}: Partial<Props>) {
  const diff = max - min
  const last = inclusive ? 1 : 0
  const period = Math.abs(diff) + last
  const periodIndex = Math.floor(index / period)
  const evenPeriod = mod(periodIndex, 2) === 0
  const forward = (!alternate || evenPeriod) !== reverse
  const periodSign = forward ? 1 : -1
  const start = forward ? min : max
  const sign = Math.sign(diff) * periodSign
  return start + sign * mod(index, period)
}
