import {Inputs, Ref, useEffect} from 'preact/hooks'

export const useEvent = <K extends keyof HTMLElementEventMap> (
  ref: Ref<HTMLElement>,
  type: K,
  listener: (this: HTMLElement, ev: HTMLElementEventMap[K]) => any,
  inputs?: Inputs
) => {
  useEffect(() => {
    ref.current.addEventListener(type, listener)
    return () => {
      ref.current.removeEventListener(type, listener)
    }
  }, inputs)
}
