import {font, Synth} from './synth'
import {cached} from '@dipl/utils-cached'
import {midiPitchFromString} from '@dipl/utils-pitch'
// @ts-ignore
import loadAudio from 'audio-loader'

export type Loader = (ac: BaseAudioContext, config: unknown) => Promise<Synth>

export function Loader (): Loader {

  // NOTE: This ac is only used for decoding audio.
  // AudioBuffer can freely be shared between contexts.
  const ac = new AudioContext()
  const loadFont = cached(async (config: unknown) => {

    // Ensure the audio is loaded as a map from pitch to buffers.
    let audio: AudioBuffer | Record<PropertyKey, AudioBuffer> = (
      await loadAudio(config, {context: ac})
    )
    if (audio instanceof AudioBuffer) {
      audio = {[0]: audio} // use arbitrary default pitch of 0
    }

    const map = new Map<number, AudioBuffer>()
    Object.entries(audio).map(([key, data]) => {
      map.set(midiPitchFromString(key), data)
    })

    return {
      keys: Array.from(map.keys()),
      get: (key: number): AudioBuffer => {
        const value = map.get(key)
        if (value) return value
        throw new Error('key not found: ' + key)
      }
    }
  })

  // NOTE: This ac is used for building the actual audio graph.
  // This allows us to serve multiple AudioContexts with a single loader.
  // Use case: Preview audio then render it to file with OfflineAudioContext.
  return async (ac: BaseAudioContext, config: unknown): Promise<Synth> => {
    return font(await loadFont(config))(ac)
  }
}
