export type PeekingIterator<T> = Iterator<T> & {
  readonly peek: () => {
    value: T
    done?: boolean
  }
}

export const peekingIterator = <T> (it: Iterator<T>): PeekingIterator<T> => {
  let current = it.next()
  return {
    peek: () => current,
    next: () => {
      const temp = current
      current = it.next()
      return temp
    }
  }
}

export const peeking = <T> (it: Iterable<T>): PeekingIterator<T> => {
  return peekingIterator(it[Symbol.iterator]())
}

// take items from the iterator until either
// (1) the next value does not match the predicate
// (2) the iterator is done
export const takeWhile = function* <T> (it: PeekingIterator<T>, predicate: (x: T) => boolean): Iterable<T> {
  for (
    // try first value
    let {value, done} = it.peek();

    // iterator not empty, and item matches predicate
    !done && predicate(value);

    // remove value, try next item
    it.next(), {value, done} = it.peek()
  ) {
    // emit current event
    yield value
  }
}

