import {decompose, modulo} from '@dipl/utils-modulo'

const modePattern = (mode: number) => {
  const major = [2, 2, 1, 2, 2, 2, 1]
  const rotate = modulo(mode, 7)
  return [
    ...major.slice(rotate),
    ...major.slice(0, rotate)
  ]
}

export const chromatic = (mode: number) => {
  const pattern = modePattern(mode)
    .reduce((a, x, i) => [...a, a[i] + x], [0])

  return (value: number) => {
    const [degree, alter] = decompose(value, 1)
    const [octaves, index] = decompose(degree, 7)
    return pattern[index] + 12 * octaves + alter * 2
  }
}

export const diatonic = (mode: number) => {
  const pattern = modePattern(mode)
    .reduce((a: number[], x, i) => {
      if (x === 1) {
        return [...a, i]
      }
      /* istanbul ignore next */
      if (x === 2) {
        return [...a, i, i + 0.5]
      }
      /* istanbul ignore next */
      throw new Error('unexpected number in diatonic pattern')
    }, [])

  return (x: number) => {
    const [octaves, index] = decompose(x, 12)
    return pattern[index] + 7 * octaves
  }
}
