type Props<T> = {
  array: T[]
  compare: (a: T, b: T) => number
}

function BinarySearch<T> ({array: a, compare}: Props<T>) {
  return (x: T) => {
    // see: https://stackoverflow.com/a/29018745/925580
    let l = 0
    let r = a.length - 1
    while (l <= r) {
      const m = (l + r) >> 1
      const c = compare(x, a[m])
      if (c > 0) {
        l = m + 1
      } else if (c < 0) {
        r = m - 1
      } else {
        return m
      }
    }
    // not found, return insertion index (complement)
    return ~l
  }
}

export default BinarySearch
