import {Loader} from './loader'
import {Synth} from './synth'
import {Sound} from './sound'

export type Sampler = {
  play:  (sound: Sound, time: number) => () => void
  stop: () => void
}

export async function Sampler (
  ac: BaseAudioContext,
  score: Sound[],
  loader: Loader
): Promise<Sampler> {

  // fetch sound resources to load
  const unique = <T> (array: T[]) => Array.from(new Set(array))
  const configs = unique(score.map(x => x.play))

  // load synths
  const synths = new Map(
    await Promise.all(
      configs.map(async (config): Promise<[unknown, Synth]> => [
        config,
        await loader(ac, config)
      ])
    )
  )

  // overall processing
  const gain = ac.createGain()
  gain.connect(ac.destination)
  const compressor = ac.createDynamicsCompressor()
  compressor.connect(gain)

  // creates a node that collects all single-use sound nodes
  const connectInput = () => {
    const node = ac.createGain()
    node.connect(compressor)
    return node
  }

  // for stopping just disconnect and throw away the input node
  let input = connectInput()
  const stop = () => {
    input.disconnect()
    input = connectInput()
  }

  const play = (sound: Sound, time: number) => {
    const synth = synths.get(sound.play) as Synth
    const offsetSound = Object.create(sound)
    offsetSound.time = sound.time + time
    const node = synth(offsetSound)
    node.connect(input)
    return () => node.disconnect()
  }

  return {
    play,
    stop
  }
}
