import {useEffect, useState} from 'preact/hooks'
import {Fragment, h} from 'preact'
import {IconButton} from './IconButton'
import {MdGetApp, MdHourglassEmpty, MdSave} from 'react-icons/md'
import {Note} from '../utils'
import {Loader, render as renderAudio} from '@dipl/dom-play'
import {AudioDownloader} from './AudioDownloader'
import {CgDarkMode} from 'react-icons/cg'

export const Downloader = ({
  notes,
  loader,
  loaded,
  getFileName
}: {
  notes: Note[]
  loaded: boolean
  loader: Loader
  getFileName: () => string
}) => {
  const [version, setVersion] = useState(0)

  // Invalidate rendered audio when notes change.
  useEffect(() => setVersion(version + 1), [notes])

  return (
    <AudioDownloader
      key={version}
      fetchBuffer={() => renderAudio(notes, loader)}
      render={({data, loading, onLoad}) => (
        <Fragment>
          {loading ? (
            <IconButton
              icon={<MdHourglassEmpty/>}
              label="stop"
            />
          ) : (
            data ? (
              <a href={data} download={getFileName()}>
                <IconButton
                  icon={<MdGetApp/>}
                  label="Download MP3"
                />
              </a>
            ) : (
              <IconButton
                disabled={!loaded}
                icon={<MdSave/>}
                label="Convert to MP3"
                onAction={onLoad}
              />
            )
          )}
        </Fragment>
      )}
    />
  )
}
