import 'typeface-jetbrains-mono'
import 'typeface-roboto-slab'
import {ComponentChildren, createContext, h, RenderableProps} from 'preact'
import {useContext, useEffect, useState} from 'preact/hooks'
import {getColors} from '../colors'

export type Theme = typeof lightTheme

// language=css
const css = (theme: Theme) => `
::selection {
  background: ${theme.backgroundSelection};
}

html {
  background: ${theme.background};
  font-size: ${theme.fontSize};
}

body {
  margin: 0;
}

[data-tight="true"] > li > p {
  margin: 0;
}
`

const lightTheme = {

  font: '"Roboto Slab", sans-serif',
  fontCode: '"JetBrains Mono", monospace',
  fontSize: '14px',
  fontSizeCode: '12px',
  lineHeight: '30px',
  lineHeightCode: '30px',

  text: 'rgba(0, 0, 0, 0.75)',
  textPrimary: 'rgba(0, 0, 0, 1)',
  textSecondary: 'rgba(0, 0, 0, 0.5)',
  textDisabled: 'rgba(0, 0, 0, 0.25)',

  color: getColors(600),

  divider: 'rgba(0, 0, 0, 0.25)',

  background: '#eee',
  backgroundSelection: 'rgba(0, 0, 0, 0.25)',
  backgroundHover: 'rgba(0, 0, 0, 0.125)',

  cardRadius: '0.25rem',
  cardBackground: 'white',

  maxTextWidth: '768px',

  elevation: {
    lower: '0 1px 2px inset rgba(0, 0, 0, 0.25)',
    none: '',
    higher: '0 1px 2px rgba(0, 0, 0, 0.25)'
  }
}

const darkTheme: Theme = {
  ...lightTheme,
  text: 'rgba(255, 255, 255, 0.75)',
  textPrimary: 'rgba(255, 255, 255, 1)',
  textSecondary: 'rgba(255, 255, 255, 0.5)',
  textDisabled: 'rgba(255, 255, 255, 0.25)',

  color: getColors(500),

  divider: 'rgba(255, 255, 255, 0.125)',

  background: '#222',
  backgroundSelection: 'rgba(255, 255, 255, 0.25)',
  backgroundHover: 'rgba(255, 255, 255, 0.125)',

  cardBackground: '#333',

  elevation: {
    lower: '0 1px 1px inset rgba(0, 0, 0, 0.5)',
    none: '',
    higher: '0 1px 1px rgba(0, 0, 0, 0.5)'
  }
}

const ThemeContext = createContext(darkTheme)

const useGlobalStyle = (css: string) => {
  useEffect(() => {
    let style = document.createElement('style')
    style.appendChild(document.createTextNode(css))
    document.head.appendChild(style)
    return () => {
      document.head.removeChild(style)
    }
  }, [css])
}

export function useTheme () {
  return useContext(ThemeContext)
}

export function Theming (props: RenderableProps<{
  dark?: boolean
  render: (state: {
    theme: Theme
    dark: boolean
    setDark: (dark: boolean) => void
  }) => ComponentChildren
}>) {
  const [dark, setDark] = useState(props.dark || false)
  const theme = dark ? darkTheme : lightTheme
  useGlobalStyle(css(theme))
  return (
    <ThemeContext.Provider value={theme}>
      <div class="theming" style={{
        color: theme.text,
        fontFamily: theme.font,
        fontSize: theme.fontSize,
        lineHeight: theme.lineHeight
      }}>
        {props.render({theme, dark, setDark})}
      </div>
    </ThemeContext.Provider>
  )
}
