const range = (a: number, b: number) => {
  return new Array(b - a + 1).fill(0).map((_, i) => a + i)
}

// see: https://www.midi.org/specifications-old/item/gm-level-1-sound-set
// noinspection JSUnusedGlobalSymbols
export const
  piano = range(1, 8),
  chromaticPercussion = range(9, 16),
  organ = range(17, 24),
  guitar = range(25, 32),
  bass = range(33, 40),
  strings = range(41, 56),
  brass = range(57, 64),
  reed = range(65, 72),
  // NOTE: 'pipe' collides with 'pipe' function of core lib.
  pipes = range(73, 80),
  synthLead = range(81, 88),
  synthPad = range(89, 96),
  synthFx = range(97, 104),
  ethnic = range(105, 112),
  percussive = range(113, 120),
  soundEffects = range(121, 128)
