import * as React from 'preact'
import {Fragment, h, VNode} from 'preact'
import {useMemo, useRef, useState} from 'preact/hooks'
import {Loader} from '@dipl/dom-play'
import {LineView} from './LineView'
import {NoteView} from './NoteView'
import {Crosshair} from './Crosshair'
import {Downloader} from './Downloader'
import {Cursor} from './Cursor'
import {Player} from './Player'
import {Layout} from './Layout'
import {usePlayer} from '../hooks/usePlayer'
import {useEvent} from '../hooks/useEvent'
import {
  Box,
  boxToUnit,
  Geometry,
  getFreqValues,
  getIdealBox,
  getTimeValues,
  Note,
  Pair,
  unitToBox
} from '../utils'

export type VisProps<Y extends Note, X> = {
  axiom: X
  notes: (x: X) => Y[]
  lines: (ys: Y[]) => (x: X) => Geometry[]
  bound: (ys: Y[]) => (x: X) => Geometry[]
  input: (axiom: X, setAxiom: (axiom: Partial<X>) => void) => VNode
}

export const Vis = <Y extends Note, X> (props: VisProps<Y, X>) => {

  const [loader] = useState(() => Loader())
  const [axiom, setAxiom] = useState<X>(props.axiom)

  const data = useMemo(() => {
    const notes: Y[] = props.notes(axiom)
    const bound: Box = getIdealBox(props.bound(notes)(axiom))
    const lines = props.lines(notes)(axiom)
    return {
      notes,
      scale: boxToUnit(bound),
      unscale: unitToBox(bound),
      ticks: [
        getTimeValues(lines),
        getFreqValues(lines)
      ] as Pair<number[]>
    }
  }, [axiom])

  const input = (props.input || (() => <Fragment/>))(
    axiom,
    part => setAxiom({...axiom, ...part})
  )

  const playerState = usePlayer(loader, data.notes, [axiom])
  const visRef = useRef<HTMLElement>()

  // Focus play button when clicking visualization.
  // Allows controlling play/stop with space bar.
  // FIXME: do not use ID but refs
  const [playButtonId] = useState('play-' + String(Math.random()).substr(2))
  useEvent(visRef, 'click', () => {
    const playButton = document.getElementById(playButtonId)
    if (!playButton) return
    playButton.focus()
  })

  return (
    <Layout
      header={
        <div style={{
          display: 'flex'
        }}>
          <Player
            id={playButtonId}
            loaded={playerState.ready}
            onPlay={() => playerState.player?.play()}
            onStop={() => playerState.player?.stop()}
          />
          <Downloader {...{
            loaded: playerState.ready,
            loader: playerState.loader,
            notes: data.notes,
            getFileName: () => 'my-song.mp3'
          }}/>
        </div>
      }
      aside={input}
      main={
        <section
          ref={visRef as any}
          style={{
            position: 'absolute',
            top: 0,
            left: 0,
            bottom: 0,
            right: 0,
            overflow: 'hidden'
          }}>
          {data.ticks[0].map(value => (
            <LineView {...{
              value,
              scale: data.scale,
              axis: 0
            }}/>
          ))}
          {data.ticks[1].map(value => (
            <LineView {...{
              value,
              scale: data.scale,
              axis: 1
            }}/>
          ))}
          {data.notes.map(note => (
            <NoteView
              note={note}
              scale={data.scale}
            />
          ))}
          <Cursor
            scale={data.scale}
            notes={data.notes}
            getTime={() => playerState.player?.getState().time}
          />
          <Crosshair
            element={visRef}
            ticks={data.ticks}
            scale={data.scale}
            unscale={data.unscale}
            onSelect={([time]) => playerState.player?.jump(time)}
          />
        </section>
      }
    />
  )
}
