import {Sound} from './sound'
import {Loader} from './loader'
import {Timeline} from '@dipl/utils-timeline'
import Scheduler from '@dipl/utils-scheduler'
import {Sampler} from './sampler'

export type Player = {
  play: () => void
  stop: () => void
  jump: (time: number) => void
  getState: () => {
    time: number
    playing: boolean
  }
}

export async function Player (
  score: Sound[],
  loader: Loader
): Promise<Player> {

  const ac = new AudioContext()
  const player = await Sampler(ac, score, loader)
  const timeline = Timeline(() => ac.currentTime)

  // create time-accurate scheduler
  const scheduler = Scheduler({
    events: score,
    getTime: x => x.time,
    delay: 0.1,
    overlap: 0.1,
    interval: 1.0,
    callback: (sound, time) => {
      player.play(sound, time)
    }
  })

  const play = () => {
    const {time} = timeline.getState()
    scheduler.play(time, ac.currentTime)
    timeline.play()
  }

  const stop = () => {
    scheduler.stop()
    player.stop()
    timeline.stop()
  }

  const jump = (time: number) => {
    timeline.jump(time)
    const {playing} = timeline.getState()
    if (playing) {
      scheduler.stop()
      player.stop()
      scheduler.play(time, ac.currentTime)
    }
  }

  return {play, stop, jump, getState: timeline.getState}
}
