import Adsr from '@dipl/utils-adsr'
import BinarySearch from '@dipl/utils-binary-search'
import {Sound} from './sound'

export type Synth = (sound: Sound) => AudioNode

export type SynthFactory = (ac: BaseAudioContext) => Synth

export type SoundFont = {
  keys: number[]
  get: (key: number) => AudioBuffer
}

const envelope = (ac: BaseAudioContext, sound: Sound, source: AudioScheduledSourceNode) => {
  const gain = ac.createGain()

  const envelope = Adsr(sound)
  if (!envelope) return gain // will be silent
  envelope.points.forEach(([time, value]) => {
    gain.gain.linearRampToValueAtTime(
      Math.max(Number.EPSILON, value),
      time
    )
  })

  source.connect(gain)
  source.start(envelope.start)
  source.stop(envelope.end)
  return gain
}

export const buffer = (buffer: AudioBuffer): SynthFactory => (
  ac => sound => {
    const node = ac.createBufferSource()
    node.buffer = buffer
    node.detune.setValueAtTime(sound.detune || 0, 0)
    return envelope(ac, sound, node)
  }
)

export const font = (font: SoundFont): SynthFactory => (
  ac => {
    const compare = (a: number, b: number) => a - b

    const instruments = new Map<number, Synth>()
    font.keys.map(key => {
      const instrument = buffer(font.get(key))(ac)
      instruments.set(key, instrument)
    })

    const nums = font.keys.sort(compare)
    const find = BinarySearch({compare, array: nums})

    const silence = ac.createBufferSource()

    return sound => {
      const index = find(sound.freq)

      // frequency falls between two sounds
      let detune = 0
      let key = nums[index]
      if (index < 0) {
        key = nums[-index]
      }

      const instrument = instruments.get(key)
      if (!instrument) return silence

      const changedSound = Object.create(sound)
      changedSound.detune = detune
      return instrument(changedSound)
    }
  }
)
