import {h, VNode} from 'preact'
import {useState} from 'preact/hooks'
// FIXME: TypeScript sometimes does not recognize this module.
// @ts-ignore
import encodeAudio from 'audio-encoder'

const bufferToMp3 = (buffer: AudioBuffer): Promise<Blob> => {
  return new Promise(resolve => {
    const bitRate = 256
    encodeAudio(buffer, bitRate, () => {}, (blob: Blob) => {
      resolve(blob)
    })
  })
}

const audioBufferToUrl = async (buffer: AudioBuffer): Promise<string> => {
  const blob = await bufferToMp3(buffer)
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.readAsDataURL(blob)
    reader.onload = () => resolve(reader.result as string)
    reader.onerror = () => reject()
  })
}

export const AudioDownloader = ({
  fetchBuffer,
  render
}: {
  fetchBuffer: () => Promise<AudioBuffer>
  render: (props: {
    data: string | null
    loading: boolean
    onLoad: () => void
  }) => VNode
}) => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState<string | null>(null)

  const update = async () => {
    setData(null)
    setLoading(true)
    const buffer = await fetchBuffer()
    setData(await audioBufferToUrl(buffer))
    setLoading(false)
  }

  return render({
    data,
    loading,
    onLoad: () => update()
  })
}
