import {h} from 'preact'
import {IconButton} from './IconButton'
import {MdPause, MdPlayArrow} from 'react-icons/md'
import {useState} from 'preact/hooks'

export const Player = (props: {
  id?: string
  loaded: boolean
  onPlay: () => void
  onStop: () => void
}) => {
  const [playing, setPlaying] = useState(false)
  return (
    <IconButton
      // FIXME: should use ref with some combination of forwardRef
      // unfortunately we need the ref inside IconButton as well.
      id={props.id}
      disabled={!props.loaded}
      icon={!playing
        ? <MdPlayArrow/>
        : <MdPause/>
      }
      label={playing ? 'Pause' : 'Play'}
      onAction={() => {
        setPlaying(!playing)
        playing
          ? props.onStop()
          : props.onPlay()
      }}
    />
  )
}
