import {ComponentChildren, h} from 'preact'
import {useTheme} from './Theming'

export type InputLabelProps<T> = {
  label: string
  children: ComponentChildren
}

export function InputLabel<T> (props: InputLabelProps<T>) {
  const theme = useTheme()
  return (
    <label style={{
      display: 'flex',
      flexShrink: 0,
      alignItems: 'stretch',
      color: theme.textSecondary,
      margin: '0.25rem'
    }}>
      <div
        style={{
          width: '120px',
          textAlign: 'left',
          marginRight: '0.5rem'
        }}>
        {props.label}
      </div>
      <div style={{
        display: 'flex',
        flex: 1
      }}>
        {props.children}
      </div>
    </label>
  )
}
