import {Sound} from './sound'
import {Loader} from './loader'
import {Sampler} from './sampler'

export async function render (
  score: Sound[],
  loader: Loader
): Promise<AudioBuffer> {

  // Note: We ignore the release attribute and cut off immediately.
  const getMaxTime = (score: Sound[]) => score
    .map(s => s.time + s.span)
    .reduce((a, b) => Math.max(a, b), 0)

  // Compute length and add some delay after the piece for good measure.
  const padding = 1.0 // seconds
  const sampleRate = 44100
  const totalDuration = getMaxTime(score) + padding
  const length = sampleRate * totalDuration

  // Create offline context for rendering.
  const channels = 2
  const ac = new OfflineAudioContext(channels, length, sampleRate)

  // NOTE: OfflineAudioContext.currentTime is zero until started.
  // We can take all the time we need to schedule the events.
  // NOTE: Any events before time 0 will be played at time 0 (FireFox).
  const {play} = await Sampler(ac, score, loader)
  score.forEach(sound => play(sound, 0))

  return ac.startRendering()
}
