import {peekingIterator, takeWhile} from '@dipl/utils-peeking-iterator'

type Cancel = void | (() => void)

export type Params<E> = {
  events: E[]
  callback: (event: E, origin: number) => Cancel
  getTime: (e: E) => number
}

export type Options = {
  setInterval: (f: () => void, delay: number) => unknown
  clearInterval: (handle: any) => void
  delay: number
  interval: number
  overlap: number
}

export const defaults: Options = {
  interval: 1,
  overlap: 0,
  delay: 0,
  setInterval: global.setInterval,
  clearInterval: global.clearInterval
}

export type Config<E> = Params<E> & Partial<Options>

export default function Scheduler<E> (config: Config<E>) {
  const {
    events,
    callback,
    getTime,
    delay,
    interval,
    overlap,
    setInterval,
    clearInterval
  } = {...defaults, ...config}

  if (interval <= 0) {
    throw new Error('interval must be positive')
  }
  if (overlap < 0) {
    throw new Error('overlap must not be negative')
  }
  if (delay < 0) {
    throw new Error('delay must not be negative')
  }

  // Events will be removed chronologically
  const sorted = events.sort((a, b) => getTime(a) - getTime(b))

  // Initialize the iterator
  const createIterator = () => peekingIterator(sorted[Symbol.iterator]())
  let it = createIterator()

  // Remove all before specified time
  const takeBefore = (time: number) => {
    // NOTE: We MUST evaluate the iterator here (via Array.from)
    // otherwise, the elements are not actually removed.
    return Array.from(takeWhile(it, e => getTime(e) < time))
  }

  let cancel: Cancel[] = []

  // Cancel everything and reset iterator
  const stop = () => {
    cancel.forEach(f => f ? f() : null)
    cancel = []
    it = createIterator()
  }

  // Start periodic scheduling
  // time: time offset in the event timeline
  // now: current time in the external clock
  const play = (time: number, now: number) => {

    // the origin anchors an hypothetical event at time zero to the actual time
    // on the clock in such a way that the event at time 'time' happens now
    const origin = now - time + delay

    // schedule events in upcoming time frame
    let prev = time

    // throw away any events before the first frame
    takeBefore(prev)

    const cycle = () => {
      const next = prev + interval + overlap
      for (const event of takeBefore(next)) {
        cancel.push(callback(event, origin))
      }
      prev = next
    }

    // call initially
    cycle()

    // call periodically
    const handle = setInterval(cycle, interval * 1000)
    cancel.push(() => {
      clearInterval(handle)
    })
  }

  return {play, stop}
}
