const _meter = (beats: number, beatType: number) => ({
  beats,
  beatType
})

// noinspection JSUnusedGlobalSymbols
export const
  twoHalf = _meter(2, 2),
  twoQuarter = _meter(2, 4),
  threeQuarter = _meter(3, 4),
  threeEighth = _meter(3, 8),
  fourQuarter = _meter(4, 4),
  fiveQuarter = _meter(5, 4),
  sevenQuarter = _meter(5, 4),
  sixEighth = _meter(6, 8),
  nineEighth = _meter(9, 8),
  twelveEighth = _meter(12, 8),
  commonTime = fourQuarter,
  allaBreve = twoHalf,
  cutTime = twoHalf
