import {ComponentChildren, h} from 'preact'
import {useHover} from '../hooks/useHover'
import {useRef} from 'preact/hooks'
import {useTheme} from './Theming'
import {useFocus} from '../hooks/useFocus'

export function InputButton<T> (props: {
  children?: ComponentChildren
  onClick: () => void
  rounded?: boolean
}) {
  const ref = useRef<HTMLElement>()
  const hover = useHover(ref)
  const focus = useFocus(ref)
  const theme = useTheme()
  return (
    <button
      ref={ref as any}
      onClick={props.onClick}
      style={{
        borderRadius: props.rounded ? theme.cardRadius : 0,
        border: props.rounded ? `1px solid ${theme.divider}` : 'none',
        background: hover || focus ? theme.backgroundHover : 'none',
        color: focus ? theme.textPrimary : theme.textSecondary,
        transition: 'background 0.125s, color 0.125s',
        cursor: 'pointer',
        display: 'flex',
        alignItems: 'center',
        textAlign: 'left',
        fontSize: 'inherit',
        padding: !props.rounded ? '0.5rem' : `0.25rem 0.5rem`,
        fontFamily: 'inherit',
        outline: 'none',
        margin: props.rounded ? '0.25rem' : 0
      }}>
      {props.children}
    </button>
  )
}
