import linear from '@dipl/utils-linear'

export type Pair<T = number> = [T, T]

export type Scale = (x: number, w: number) => number

export type Dim = number | Pair

export type Box = Pair<Pair>

export type Geometry = {
  time: number
  span: number
  freq: number
}

export type Note = Geometry & {
  color: any
  play: string
  detune: number
  gain: number
  attack: number
  decay: number
  sustain: number
  release: number
}

const extent = (ns: number[]): Pair => {
  return [Math.min(...ns), Math.max(...ns)]
}

const pad = ([a, b]: Pair, pad: number): Pair => {
  return [a - pad, b + pad]
}

const unique = (ns: number[]): number[] => {
  return Array.from(new Set(ns.filter(x => !isNaN(x))))
}

export const getTimeValues = (notes: Geometry[]): number[] => {
  const head: number[] = notes.map(x => x.time)
  const tail: number[] = notes.map(x => x.time + x.span)
  return unique([...head, ...tail])
}

export const getFreqValues = (notes: Geometry[]): number[] => {
  return unique(notes.map(x => x.freq))
}

export const getBoundingBox = (notes: Geometry[]): Box => {
  return [
    extent(getTimeValues(notes)),
    extent(getFreqValues(notes))
  ]
}

export const boxToUnit = ([x, y]: Box, flipY: boolean = false): Pair<Scale> => {
  return [
    linear(x, [0, 1]),
    linear(y, flipY ? [1, 0] : [0, 1])
  ]
}

export const unitToBox = ([x, y]: Box, flipY: boolean = false): Pair<Scale> => {
  return [
    linear([0, 1], x),
    linear(flipY ? [1, 0] : [0, 1], y)
  ]
}

export const getIdealBox = (notes: Geometry[]): Box => {
  const [x, y] = getBoundingBox(notes)
  return [
    pad(x, 0.5),
    pad(y, 0.5)
  ]
}
