import {Ref} from 'preact/hooks'
import {usePointer} from './usePointer'
import {useHover} from './useHover'
import {Pair, Scale} from '../utils'

const findNearest = (x: number, ticks: number[]): number => {
  if (!ticks.length) return x
  const i1 = ticks.findIndex(xi => x <= xi)
  if (i1 < 0) return ticks[ticks.length - 1]
  if (i1 === 0) return ticks[0]
  const x0 = ticks[i1 - 1]
  const x1 = ticks[i1]
  const d0 = x - x0
  const d1 = x1 - x
  return d0 < d1 ? x0 : x1
}

export function useCrosshair (props: {
  element: Ref<HTMLElement>
  ticks: Pair<number[]>
  scale: Pair<Scale>
}) {
  const over = useHover(props.element)
  const pointer = usePointer(props.element)

  const hover: Pair = [
    props.scale[0](pointer[0], 1),
    props.scale[1](pointer[1], 1)
  ]

  // NOTE: Values must be sorted for 'findNearest'
  const ticks: Pair<number[]> = [
    props.ticks[0].sort((a, b) => a - b),
    props.ticks[1].sort((a, b) => a - b)
  ]

  const ghost: Pair = [
    findNearest(hover[0], ticks[0]),
    findNearest(hover[1], ticks[1])
  ]

  return {
    over,
    hover,
    ghost
  }
}
