import SeedRandom from 'seed-random'
import {Exp, fail, Fun, Opt, Res, res, seq, set, use, val} from '@dipl/lib-core'
import {floor, gte, linear} from '@dipl/lib-math'
import weighted from '@dipl/utils-weighted'

export type Seed = string | number

export type Rng = {rng: () => number}

export function choice<Y, X extends Rng> (...fs: Exp<Y, X>[]): Fun<Y, X> {
  return x => {
    const ys = fs.map(f => val(f, x))
    const candidates = weighted(ys, () => 1)
    if (!candidates) {
      throw new Error('choice: no candidates')
    }
    return candidates(val(rand, x))
  }
}

export function pick<Y, X extends Rng> (...fs: Opt<Y, X>[]): Fun<Res<Y>, X> {
  return x => {
    const rs = fs.map(f => res(f, x))
    const candidates = weighted(rs, r => r.weight)
    if (candidates == null) {
      return fail(`pick: no options to pick from`)
    }
    return candidates(val(rand, x))
  }
}

export function pr<X extends Rng> (p: Exp<number, X>): Fun<boolean, X> {
  return gte(p, rand)
}

export function rand<X extends Rng> (x: X): number {
  return x.rng()
}

export function rng<X> (...seeds: Exp<Seed, X>[]): Fun<() => number, X> {
  // NOTE: do not use 'use' here, as we want to return a function.
  // 'use' uses val internally and will evaluate the function!
  return x => {
    const s = val(seq(...seeds), x)
    return SeedRandom(s.join(' '))
  }
}

export function seed<X extends Rng> (...seeds: Exp<Seed, X>[]): Fun<X, X> {
  return set(x => ({
    rng: rng(...seeds)(x)
  }))
}

export function uniform<X extends Rng> (min: Exp<number, X>, max: Exp<number, X>): Fun<number, X> {
  return linear((x: X) => x.rng(), min, max)
}

export function uniformInt<X extends Rng> (min: Exp<number, X>, max: Exp<number, X>): Fun<number, X> {
  return floor(uniform(min, max))
}

export function normal<X extends Rng> (mean: Exp<number, X> = 0, sd: Exp<number, X> = 1): Fun<number, X> {
  return x => {
    const u = val(rand, x)
    const v = val(rand, x)
    const y = Math.sqrt(-2 * Math.log(1 - u)) * Math.cos(2 * Math.PI * v)
    return val(sd, x) * y + val(mean, x)
  }
}
