const last = <T>(array: T[]): T => array[array.length - 1]

export type Result<X> = null | ((rand: number) => X)

export default function weighted <X> (
  values: X[],
  getWeight: (x: X) => number
): Result<X> {

  // candidate must have valid weight
  const candidates = values
    .map(value => {
      const weight = getWeight(value)
      if (weight < 0) throw new Error('weight must not be negative')
      if (isNaN(weight)) throw new Error('weight must be a number')
      if (weight === Infinity) throw new Error('weight must not be Infinity')
      return {value, weight}
    })
    .filter(({weight}) => weight > 0)

  // must find at least one candidate
  if (!candidates.length) return null

  // cumulative weight sums
  const sums = candidates
    .reduce((a, {weight}) => [...a, last(a) + weight], [0])

  // pick weighted
  return (rand: number) => {
    if (rand < 0) throw new Error('random number must not be negative')
    if (rand >= 1) throw new Error('random number must be less than 1')
    const t = last(sums) * rand
    const i = sums.slice(1).findIndex(x => t < x)
    return candidates[i].value
  }
}
