import {Inputs, useEffect, useRef, useState} from 'preact/hooks'
import {Note} from '../utils'
import {Loader, Player} from '@dipl/dom-play'

export function usePlayer (loader: Loader, notes: Note[], inputs?: Inputs) {
  const [ready, setReady] = useState(false)
  const playerRef = useRef<Player | undefined>()

  useEffect(() => {
    setReady(false)

    Player(notes, loader)
      .then(player => {
        playerRef.current = player
        setReady(true)
      })
      .catch(error => {
        alert('Player failed to load. Check browser console for error messages.')
        console.error(error)
      })

    return () => {
      if (!playerRef.current) return
      playerRef.current.stop()
    }
  }, inputs)

  return {
    ready,
    loader,
    player: playerRef.current
  }
}
