type Options = {
  time: number
  span: number
  gain: number
  attack: number
  decay: number
  sustain: number
  release: number
  silence: boolean
}

type Result = {
  start: number
  end: number
  points: [number, number][]
}

const num = (value: unknown, fallback: number): number => {
  const n = Number(value)
  return Math.max(0, isNaN(n) ? fallback : n)
}

const defaults: Options = {
  silence: false,
  time: 0,
  span: 1,
  gain: 1,
  attack: 0,
  decay: 0,
  sustain: 1,
  release: 0
}

const validate = (x: Partial<Options>): Options | null => {
  const gain = num(x.gain, defaults.gain)
  const silent = gain === 0
  if (silent && x.silence) return null
  return {
    gain,
    silence: x.silence || defaults.silence,
    time: num(x.time, defaults.time),
    span: num(x.span, defaults.span),
    attack: num(x.attack, defaults.attack),
    decay: num(x.decay, defaults.decay),
    sustain: num(x.sustain, defaults.sustain),
    release: num(x.release, defaults.release)
  }
}

const calculate = (options: Options): Result => {
  const {time, span, gain, attack, decay, sustain, release} = options
  return {
    start: time,
    end: time + span + release,
    points: [
      [time, 0],
      [time + attack, gain],
      [time + attack + decay, sustain * gain],
      [time + span, sustain * gain],
      [time + span + release, 0]
    ]
  }
}

const Adsr = (options: Partial<Options> = {}): Result | null => {
  const opts = validate(options)
  return opts == null ? null : calculate(opts)
}

export default Adsr
