import {Pair, Scale} from '../utils'
import {Shape} from './Shape'
import {Fill} from './Fill'
import {h} from 'preact'
import {useTheme} from './Theming'

export function LineView (props: {
  axis: 0 | 1
  value: number
  scope?: Pair
  color?: string
  scale: Pair<Scale>
}) {
  // NOTE: If not explicit scope is given, line should be infinite.
  // Since we cannot extend the line towards infinity, we will instead
  // modify the scale so that it maps the scope axis to 0-100% of the element.
  const scope = props.scope || [0, 1]
  const scale: Pair<Scale> = props.scope
    ? props.scale
    : props.axis === 1
      ? [(x: number) => x, props.scale[1]]
      : [props.scale[0], (x: number) => x]

  const theme = useTheme()
  const {color = theme.divider} = props

  return (
    <Shape
      axis={props.axis}
      scale={scale}
      geometry={[props.value, scope]}>
      <Fill style={{
        ...(props.axis === 0 ? {
          borderLeft: '1px solid ' + color
        } : {
          borderTop: '1px solid ' + color
        })
      }}/>
    </Shape>
  )
}
