import {binary, Exp, seq, Seq, ternary, unary, val} from '@dipl/lib-core'
import {modulo, quotient} from '@dipl/utils-modulo'

const reduce = <T> (f: (a: T, b: T) => T, n0: T) => {
  return <X> (...ns: Exp<Seq<T>, X>[]) => (
    (x: X): T => val(seq(...ns), x).reduce(f, n0)
  )
}

export const

  // basic
  neg = unary((n: number) => -n),
  inc = unary((n: number) => n + 1),
  dec = unary((n: number) => n - 1),
  sum = reduce((a, b) => a + b, 0),
  prod = reduce((a, b) => a * b, 1),
  frac = binary((a: number, b: number) => a / b),
  diff = binary((a: number, b: number) => a - b),

  // interval
  linear = ternary((t: number, a: number, b: number) => a + t * (b - a)),
  clamp = ternary((t: number, a: number, b: number) => Math.min(b, Math.max(a, t))),

  // modulo
  mod = binary(modulo),
  quot = binary(quotient),

  // magnitude
  sign = unary(Math.sign),
  abs = unary(Math.abs),
  floor = unary(Math.floor),
  ceil = unary(Math.ceil),
  round = unary(Math.round),

  // exponential
  exp = unary(Math.exp),
  log = unary(Math.log),
  log2 = unary(Math.log2),
  sqrt = unary(Math.sqrt),
  pow = binary(Math.pow),

  // trigonometry
  sin = unary(Math.sin),
  cos = unary(Math.cos),
  tan = unary(Math.tan),

  // comparison
  eq = binary(<T> (a: T, b: T) => a == b),
  max = reduce((a, b) => Math.max(a, b), -Infinity),
  min = reduce((a, b) => Math.min(a, b), Infinity),
  lt = binary((a: number, b: number) => a < b),
  lte = binary((a: number, b: number) => a <= b),
  gte = binary((a: number, b: number) => a >= b),
  gt = binary((a: number, b: number) => a > b),

  // booleans
  not = unary((a: boolean) => !a),
  and = reduce<boolean>((a, b) => a && b, true),
  or = reduce<boolean>((a, b) => a || b, false)
