export function cached<K, V> (
  load: (key: K) => Promise<V>
) {
  const pending = new Map<K, Promise<V>>()
  const ready = new Map<K, V>()
  const error = new Map<K, Error>()

  return (key: K): Promise<V> => {
    if (pending.has(key)) {
      return pending.get(key) as Promise<V>
    }
    if (error.has(key)) {
      error.delete(key)
    }
    if (ready.has(key)) {
      return Promise.resolve(ready.get(key) as V)
    }
    return new Promise((resolve, reject) => {
      const promise = load(key)
      pending.set(key, promise)
      promise
        .then(v => {
          ready.set(key, v)
          resolve(v)
        })
        .catch(e => {
          error.set(key, e)
          reject(e)
        })
        .finally(() => {
          pending.delete(key)
        })
    })
  }
}
