import {IconButton} from './IconButton'
import {CgDarkMode} from 'react-icons/cg'
import {h} from 'preact'

export function ThemeToggle ({
  dark = false,
  onChange = () => {}
}: {
  dark?: boolean
  onChange?: (dark: boolean) => void
}) {
  return (
    <IconButton
      onAction={() => onChange(!dark)}
      icon={<CgDarkMode/>}
      label={dark
        ? 'Use light theme'
        : 'Use dark theme'}
    />
  )
}
