export type Timeline = {
  play: () => void
  stop: () => void
  jump: (time: number) => void
  getState: () => {
    playing: boolean
    time: number
  }
}

export function Timeline (clock: () => number): Timeline {
  let playing = false
  // While playing: time = clock - marker.
  // While not playing: time = marker.
  let marker = 0
  return {
    play: () => {
      if (playing) return
      playing = true
      // We need to start before the clock starts
      marker = clock() - marker
    },
    stop: () => {
      if (!playing) return
      playing = false
      marker = clock() - marker
    },
    jump: (time: number) => {
      marker = playing
        ? clock() - time
        : time
    },
    getState: () => {
      return {
        playing,
        time: playing
          ? clock() - marker
          : marker
      }
    }
  }
}
