import {ComponentChildren, h, RenderableProps} from 'preact'
import * as React from 'preact/compat'
import {useRef} from 'preact/hooks'
import {useHover} from '../hooks/useHover'
import {useFocus} from '../hooks/useFocus'
import {useTheme} from './Theming'

export const IconButton = (props: RenderableProps<{
  id?: string
  label: string
  icon: ComponentChildren
  onAction?: () => void
  disabled?: boolean
}>) => {
  const {
    label,
    icon,
    onAction = () => void(0),
    disabled = false,
    ...rest
  } = props

  const ref = useRef<HTMLElement>()
  const hover = useHover(ref)
  const focus = useFocus(ref)
  const theme = useTheme()

  const getColor = () => {
    if (disabled) return theme.textDisabled
    return focus
      ? theme.textPrimary
      : theme.textSecondary
  }

  const getBackground = () => {
    if (disabled) return 'none'
    return hover || focus
      ? theme.backgroundHover
      : 'none'
  }

  const getCursor = () => {
    return disabled ? 'normal' : 'pointer'
  }

  return (
    <button
      {...rest}
      disabled={disabled}
      title={label}
      ref={ref as any}
      style={{
        border: 'none',
        cursor: getCursor(),
        padding: 0,
        background: getBackground(),
        transition: 'color 0.125s, background: 0.125s',
        position: 'relative',
        color: getColor(),
        fontSize: 'inherit',
        fontFamily: 'inherit',
        lineHeight: 'inherit',
        margin: '0.5rem',
        width: '3rem',
        height: '3rem',
        borderRadius: '3rem',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        outline: 'none'
      }}
      onClick={() => onAction()}>
      <span style={{
        position: 'relative',
        zIndex: 1,
        fontSize: '2rem'
      }}>
        {icon}
      </span>
    </button>
  )
}
