import {Note, Pair, Scale} from '../utils'
import {Shape} from './Shape'
import {Fill} from './Fill'
import {h} from 'preact'
import {Theme, useTheme} from './Theming'

const getColor = (theme: Theme, value: string | number): string => {
  const num = Number(value)
  const isInt = !isNaN(num) && num % 1 === 0
  const n = isInt ? num : String(value)
    .split('').reduce((a, x) => a + x.charCodeAt(0), 0)
  return theme.color[n % theme.color.length]
}

export function NoteView (props: {
  note: Note
  scale: Pair<Scale>
}) {
  const note = props.note
  const theme = useTheme()
  const opacity = Math.min(1, Math.max(0, note.gain))
  const color = getColor(theme, note.color)
  const silent = note.gain <= 0
  const size = '6px'
  const common = {
    borderRadius: theme.cardRadius,
    margin: `-${size} 0 -${size} 0`,
  }
  return (
    <Shape
      scale={props.scale}
      geometry={[[note.time, note.time + note.span], note.freq]}
      interactive={true}
      onClick={() => console.log(note)}>
      <Fill style={{
        ...common,
        opacity: silent ? 1 : opacity,
        background: silent ? 'none' : color
      }}/>
      <Fill style={{
        ...common,
        border: `1px solid ${color}`
      }}/>
    </Shape>
  )
}
