import {render} from 'preact'
import {
  fieldset,
  inputs,
  label,
  number,
  presets,
  propKey, toggle,
  vis
} from '@dipl/dom-vis'
import {EntityDefaults, measure, repeat} from '@dipl/lib-music'
import {fork, Fun, map, set} from '@dipl/lib-core'
import {Globals, Init, MyEntity, Piece} from './grammar'

const entity: MyEntity = {
  ...EntityDefaults,
  piece: Math.round(Math.random() * 10000),
  depth: 4,
  monotony: 0.5,
  diversity: 0.5,
  layer: 0,
  label: 0,
  labels: [1],
  chord: 3,
  role: 'drum',
  chords: [],
  drums: 0,
  release: 0.1
}

const input = inputs<MyEntity>(
  fieldset('Controls', inputs(
    presets({random: Globals()}),
    propKey(label, 'piece', number(0, 10000, 1)),
    propKey(label, 'key', number(0, 12)),
    propKey(label, 'tempo', number(80, 140)),
    propKey(label, 'beats', number(2, 6)),
    propKey(label, 'beatType', number(2, 8)),
    propKey(label, 'monotony', number(0, 1, 0.01)),
    propKey(label, 'diversity', number(0, 1, 0.01)),
    propKey(label, 'depth', number(0, 5)),
    propKey(label, 'drums', number(0, 6))
  ))
)

function Lines<X extends MyEntity> (notes: X[]): Fun<X[], X> {
  return Init(
    fork(
      repeat<X>({size: measure}),
      map(notes, x => set({freq: x.freq}))
    )
  )
}

render(
  vis({
    input,
    axiom: entity,
    notes: Piece,
    lines: Lines
  }),
  document.body
)
