import {ComponentChildren, h} from 'preact'
import {useTheme} from './Theming'
import {useState} from 'preact/hooks'
import {InputButton} from './InputButton'

export type FieldsetProps<T> = {
  legend: string
  children: ComponentChildren
}

export function Fieldset<T> (props: FieldsetProps<T>) {
  const [open, setOpen] = useState(false)

  const theme = useTheme()
  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      border: 'none',
      color: theme.textSecondary,
      padding: 0,
      margin: 0,
      borderBottom: '1px solid ' + theme.divider
    }}>
      <InputButton onClick={() => setOpen(!open)}>
        <i
          style={{
            fontStyle: 'initial',
            marginRight: '1rem',
            textAlign: 'center',
            width: '16px',
            marginTop: '-0.25rem',
          }}>
          {open ? '▿' : '▹'}
        </i>
        <span>{props.legend}</span>
      </InputButton>
      {open && <div style={{
        padding: '0.25rem',
        display: 'flex',
        flexDirection: 'column',
        borderTop: '1px dashed ' + theme.divider
      }}>
        {props.children}
      </div>}
    </div>
  )
}
