import {
  diatonic,
  eighth,
  Entity,
  gleitz,
  half,
  measure,
  measures,
  overlaps,
  quarter,
  rel,
  repeat,
  split,
  translate,
  trim,
  whole
} from '@dipl/lib-music'
import {
  clone,
  Exp,
  filter,
  first,
  fork,
  Fun,
  indexed,
  map,
  noop,
  pipe,
  pipeDeep,
  range,
  Seq,
  set,
  setx,
  use,
  val,
  when
} from '@dipl/lib-core'
import {choice, pr, rand, seed, uniform} from '@dipl/lib-rng'
import {frac, lte, pow, round} from '@dipl/lib-math'
import {
  acousticGrandPiano,
  contrabass,
  I,
  II,
  III,
  IV,
  major,
  minor,
  musyngKite,
  piccolo,
  V,
  VI,
  VII,
  VIII,
  violin
} from '@dipl/lib-constants'
import * as drums from '@dipl/demo-alpha/src/drums'

export type MyEntity = Entity & {
  piece: number
  depth: number
  monotony: number
  diversity: number
  layer: number
  label: number
  labels: number[]
  chord: number
  role: 'bass' | 'lead' | 'pad' | 'drum'
  chords: MyEntity[]
  drums: number
}

function Cut<X extends Entity> (size: Exp<number, X>): Fun<X[], X> {
  return trim(choice(
    split<X>(setx({size}), noop),
    split<X>(noop, setx({size}))
  ))
}

function Rep<X extends Entity> (size: Exp<number, X>): Fun<X[], X> {
  return trim(indexed(repeat<X>({
    size,
    cover: true
  }), i => set({index: i})))
}

function Deg1<X extends Entity> (): Fun<X, X> {
  return setx({degree: choice(I, III, V, VIII)})
}

function Deg2<X extends Entity> (): Fun<X, X> {
  return setx({degree: choice(II, IV, VI, VII)})
}

function Pr<X extends Entity> (p: Exp<number, X>, f: Exp<Seq<X>, X>): Fun<X[], X> {
  return when(pr(p), f)
}

function Bass<X extends Entity> (): Fun<X[], X> {
  return pipe(
    Rep(choice<number, X>(half, quarter)),
    when(x => x.index > 0, Deg1()),
    setx({octave: choice(1, 2)}),
    setx({freq: diatonic})
  )
}

function Pad<X extends MyEntity> (): Fun<X[], X> {
  return pipe(
    setx({octave: choice(3, 4)}),
    Pr(0.5, Cut(choice<number, X>(half, quarter, eighth))),
    range(x => x.chord || 3, i => set({degree: i * 2})),
    Pr(0.5, Cut(choice<number, X>(half, quarter, eighth))),
    setx({freq: diatonic})
  )
}

function Lead<X extends MyEntity> (): Fun<X[], X> {
  return pipe(
    Pr(0.5, Cut(choice<number, X>(half, quarter, eighth))),
    Pr(0.5, Cut(choice<number, X>(half, quarter, eighth))),
    Deg1(),
    Pr(0.5, Rep(choice<number, X>(half, quarter, eighth))),
    Pr(0.5, Deg1()),
    Pr(0.25, Deg2()),
    setx({freq: diatonic})
  )
}

function Drum<X extends Entity> (): Fun<X[], X> {
  return pipe<X>(
    Rep(choice<number, X>(measure, half, quarter, eighth)),
    setx({gain: x => x.gain * x.span / val(whole, x)}),
    x => when(x.index % 2 === 1, setx({gain: x.gain / 2})),
    x => when(lte(x.span, quarter), translate<X>(choice(0, rel(1 / 2))))
  )
}

function Motif<X extends MyEntity> (): Fun<X[], X> {
  return trim((x: X) => ({
    lead: Lead<X>(),
    pad: Pad<X>(),
    bass: Bass<X>(),
    drum: Drum<X>()
  }[x.role]))
}

function Sync<X extends Entity> (p: Exp<number, X>, f: Exp<X[], X>): Fun<X[], X> {
  return use(rand, sync => pipe<X>(
    f,
    use(p, p => seed<X>(p < sync ? rand : sync))
  ))
}

function Layout<X extends MyEntity> (f: Exp<X[], X>): Fun<X[], X> {
  return pipeDeep(
    x => x.depth,
    Sync(x => x.monotony, split(clone(2))),
    f
  )
}

function Layer<X extends MyEntity> (): Fun<X[], X> {
  return pipe(
    seed(x => x.piece),
    Layout(when(pr(x => x.diversity), seed<X>(rand, x => x.layer)))
  )
}

function Label<X extends MyEntity> (): Fun<X[], X> {
  return pipe(
    x => setx({label: choice(...x.labels)}),
    x => when<X>(x.label === 0, []),
    x => seed(x.piece, x.label, x.layer)
  )
}

function Chords<X extends Entity> (): Fun<X[], X> {
  return pipe(
    setx({mode: choice(major, minor)}),
    set({chord: 3}),
    map([I, IV, V, VI], harmony => set({harmony})),
    when(x => x.harmony === V, set({chord: 4}))
  )
}

function Progression<X extends MyEntity> (f: Exp<X[], X>): Fun<X[], X> {
  return pipe<X>(
    setx({chords: Chords}),
    f,
    x => ChordAppend(choice(...x.chords))
  )
}

function ChordAppend<X extends Entity> (c: Exp<X, X>): Fun<X, X> {
  return use(c, ({harmony, key, mode, chord}) => set(
    {harmony, key, mode, chord}
  ))
}

function Chord<X extends MyEntity> (): Fun<X, X> {
  return ChordAppend<X>(
    first(filter<MyEntity, X>(x => x.chords, overlaps), noop())
  )
}

function Voices<X extends MyEntity> (): Fun<X[], X> {
  const Play = (n: number) => gleitz(musyngKite, n)
  return fork(
    setx({
      role: 'pad',
      play: Play(acousticGrandPiano),
      labels: [1, 2],
      color: 2,
      layer: rand
    }),
    setx({
      role: 'lead',
      play: Play(violin),
      labels: [1, 0, 3],
      color: 0,
      layer: 1
    }),
    setx({
      role: 'lead',
      play: Play(piccolo),
      labels: [0, 2, 3],
      color: 1,
      layer: 1
    }),
    setx({
      role: 'bass',
      play: Play(contrabass),
      labels: [1, 2],
      color: 3,
      layer: rand
    }),
    pipe(
      clone(x => x.drums),
      seed<X>(rand),
      setx({
        role: 'drum',
        play: drums.font,
        freq: choice(...drums.hats, ...drums.snares, ...drums.snares),
        labels: [1, 2],
        gain: 1 / 2,
        color: 4,
        layer: rand
      })
    )
  )
}

export function Globals<X extends MyEntity> (): Fun<X, X> {
  return setx({
    piece: round(uniform(0, 10000)),
    key: round(uniform(0, 12)),
    tempo: round(uniform(80, 140)),
    beats: choice(2, 3, 4, 6),
    beatType: choice(4, 8),
    monotony: frac(round(uniform(25, 75)), 100),
    diversity: frac(round(uniform(0, 50)), 100),
    depth: choice(2, 3, 4)
  })
}

export function Init<X extends MyEntity> (f: Fun<X[], X>): Fun<X[], X> {
  return pipe(
    x => seed(x.piece),
    setx({span: measures(pow(2, x => x.depth))}),
    f
  )
}

export function Piece<X extends MyEntity> (): Fun<X[], X> {
  return Init(pipe<X>(
    setx({chords: Progression(Layer)}),
    Voices,
    Layer,
    Label,
    Chord,
    Motif
  ))
}
