import {Fragment, h} from 'preact'
import {Ref} from 'preact/hooks'
import {LineView} from './LineView'
import {Shape} from './Shape'
import {Label} from './Label'
import {Pair, Scale} from '../utils'
import {useCrosshair} from '../hooks/useCrosshair'
import {useEvent} from '../hooks/useEvent'
import {useTheme} from './Theming'
import {decompose} from '@dipl/utils-modulo'

function AxisLabel (props: {
  axis: 0 | 1
  scale: Pair<Scale>
  value: number
  label: (value: number) => string
}) {
  const theme = useTheme()
  return <Shape
    axis={props.axis}
    scale={
      props.axis === 0
        ? [props.scale[0], x => x]
        : [x => x, props.scale[1]]
    }
    geometry={[props.value, 0]}>
      <span style={{
        ...Label({side: props.axis ? 'right' : 'top'}),
        background: theme.background,
        lineHeight: '1rem',
        padding: '0.5rem',
        borderRadius: theme.cardRadius
      }}>
        {props.label(props.value)}
      </span>
  </Shape>
}

export function Crosshair (props: {
  element: Ref<HTMLElement>
  ticks: Pair<number[]>
  unscale: Pair<Scale>
  scale: Pair<Scale>
  onSelect: (point: Pair) => void
}) {

  const state = useCrosshair({
    scale: props.unscale,
    element: props.element,
    ticks: props.ticks
  })

  useEvent(props.element, 'click', () => {
    props.onSelect(state.ghost)
  })

  // Render nothing.
  if (!state.over) {
    return <Fragment/>
  }

  const theme = useTheme()

  const AxisLine = (axis: 0 | 1) => (
    <LineView
      axis={axis}
      color={theme.divider}
      value={state.ghost[axis]}
      scale={props.scale}
    />
  )

  const timeLabel = (x: number) => {
    return x.toFixed(3)
  }

  const freqLabel = (x: number) => {
    const midi = Math.round(x)
    const cents = Math.round((x - midi) * 100)
    const fmtMidi = (x: number) => {
      const [octave, chroma] = decompose(midi, 12)
      const chromaStr = 'C C# D D# E F F# G G# A A# B'.split(' ')[chroma]
      return chromaStr + octave
    }
    const fmtCent = (x: number) => {
      const padded = String(x * 100).padStart(2, '0')
      const sign = x < 0 ? '-' : '+'
      if (x === 0) return ''
      return sign + padded
    }
    return fmtMidi(midi) + ' ' + fmtCent(cents)
  }

  return (
    <Fragment>
      {AxisLine(0)}
      {AxisLine(1)}
      <AxisLabel
        axis={0}
        value={state.ghost[0]}
        scale={props.scale}
        label={timeLabel}
      />
      <AxisLabel
        axis={1}
        value={state.ghost[1]}
        scale={props.scale}
        label={freqLabel}
      />
    </Fragment>
  )
}
