// @ts-ignore
import materialColors from 'material-colors'

export const getColors = (weight: number) => {
  const names = [
    'pink',
    'blue',
    'deepOrange',
    'green',
    'purple',
    'lightBlue',
    'lightGreen',
    'orange',
    'deepPurple',
    'cyan',
    'lime',
    'amber',
    'indigo',
    'teal',
    'yellow',
    'red'
  ]
  return names.map(name => materialColors[name][weight])
}
