import {h, RenderableProps} from 'preact'

export const FillStyle = ({}: {} = {}) => ({
  position: 'absolute',
  top: '0',
  left: '0',
  right: '0',
  bottom: '0'
})

export const Fill = (props: RenderableProps<{
  style?: object
}> = {}) => {
  return (
    <div {...props} style={{...FillStyle() as any, ...props.style}}/>
  )
}
