/**
 * A function maps a value from a set X to a value from a set Y.
 */
export type Fun<Y, X> = (x: X) => Y

/**
 * An expression is defined recursively:
 *
 * - base case: a value of a set Y
 * - induction: a function from a set X to an expression
 */
export type Exp<Y, X> = Y | ((x: X) => Exp<Y, X>)

/**
 * A result is an optional return value of a function that may have failed.
 *
 * - The success value indicates whether the value is available.
 * - The weight is optional and its interpretation depends on a consumer.
 * - The value, if successful, can be accessed with a lazy getter function.
 */
export type Res<Y> = {
  success: boolean
  weight: number
  get: () => Y
}

/**
 * An optional expression is an expression that can fail.
 */
export type Opt<Y, X> = Exp<Y | Res<Y>, X>

/**
 * An object expression treats the properties of an object Y as expressions.
 */
export type Obj<Y, X> = { [k in keyof Y]: Exp<Y[k], X> }

/**
 * A sequence is defined recursively:
 *
 * - base case: a value of a set Y
 * - induction: an array of sequences
 */
export type Seq<Y> = Y | Seq<Y>[]

/**
 * Converts an expression f into a simple function.
 */
export function exp<Y, X> (f: Exp<Y, X>): Fun<Y, X> {
  return x => {
    if (typeof f !== 'function') return f as Y
    return exp((f as (x: X) => Y)(x))(x)
  }
}

/**
 * Converts an optional expression f into a simple function.
 */
export function opt<Y, X> (f: Opt<Y, X>): Fun<Res<Y>, X> {
  return x => {
    const r = exp(f)(x)
    const isRes = !!r
      && typeof r === 'object'
      && typeof Reflect.get(r as object, 'get') === 'function'
    if (isRes) return r as Res<Y>
    return {
      success: true,
      weight: 1,
      get: () => r as Y
    }
  }
}

/**
 * Evaluates an expression f using the argument x.
 */
export function val<Y, X> (f: Exp<Y, X>, x: X): Y {
  return exp(f)(x)
}

/**
 * Evaluates an optional expression f using the argument x.
 */
export function res<Y, X> (f: Opt<Y, X>, x: X): Res<Y> {
  return opt(f)(x)
}

/**
 * Converts an object expression o into a simple function.
 */
export function obj<Y, X> (o: Exp<Obj<Y, X>, X>): Fun<Y, X> {
  return x => {
    const _o = val(o, x)
    const y: Partial<Y> = {}
    for (const k in _o) {
      // noinspection JSUnfilteredForInLoop
      const v: Exp<Y[any], X> = _o[k]
      // noinspection JSUnfilteredForInLoop
      y[k] = val(v, x)
    }
    return y as Y
  }
}

/**
 * Flattens sn array of sequence expressions s.
 */
export function seq<Y, X> (...s: Exp<Seq<Y>, X>[]): Fun<Y[], X> {
  return x => {
    return s.reduce((a: Y[], si: Exp<Seq<Y>, X>) => {
      const _si: Seq<Y> = val(si, x)
      return [...a, ...(
        Array.isArray(_si)
          ? val(seq(..._si), x)
          : [_si]
      )]
    }, [])
  }
}

/**
 * @returns a function that overwrites the properties of x with the properties of o.
 */
export function set<O, X> (o: Exp<O, X>): Fun<X & O, X> {
  return x => ({...x, ...val(o, x)})
}

/**
 * @returns a function that sets the properties of o as default values for the properties of x.
 */
export function def<Y, X extends Partial<Y>> (o: Exp<Y, X>): Fun<Y, X> {
  return x => set<X, Y>(x)(val(o, x))
}

/**
 * @remarks
 * Same as {@see set}, but treats o as an object expression.
 */
export function setx<O, X> (o: Exp<Obj<O, X>, X>): Fun<X & O, X> {
  return set(obj(exp(o)))
}

/**
 * @remarks
 * Same as {@see def}, but treats o as an object expression.
 */
export function defx<Y, X extends Partial<Y>> (o: Exp<Obj<Y, X>, X>): Fun<Y, X> {
  return def(obj(o))
}

/**
 * @returns a function that changes the value of property k with a list of mapping functions fs.
 */
export function mut<X, K extends keyof X> (k: K, ...fs: ((value: X[K]) => Exp<X[K], X>)[]): Fun<X, X> {
  return setx({
    [k]: (x: X): X[K] => fs
      .reduce((y: X[K], f) => val(f(y), x), x[k])
  })
}

/**
 * @returns a function that checks whether property k is defined.
 */
export function has<X, K extends keyof X> (k: K): Fun<boolean, X> {
  return x => k in x
}

/**
 * @returns a function that returns value of property k, or throws an error if it is not defined.
 */
export function get<X, K extends keyof X> (k: K): Fun<X[K], X> {
  return x => {
    if (k in x) return x[k]
    throw new Error(`get: key ${k} does not exist`)
  }
}

/**
 * @returns an identity function
 */
export function noop<X> (): Fun<X, X> {
  // NOTE: This definition does not seem to work:
  //   const noop = <X> (x: X) => x
  // These would not be valid:
  //   const f: F = pipe(noop)
  //   const g: F = fork(noop)
  return (x: X) => x
}

/**
 * @returns an identity function that runs the side effect f
 */
export function action<X> (f: (x: X) => void): Fun<X, X> {
  return x => {
    f(x)
    return x
  }
}

/**
 * @returns an identity function that passes the result of f to a logging function l as a side effect
 *
 * @remarks
 * Per default, the logging function used is console.log
 */
export function log<X> (f: Exp<unknown, X> = (x: X) => x, l: (message: any) => void = console.log): Fun<X, X> {
  return action(x => {
    l(val(f, x))
  })
}

/**
 * @returns a function that returns the result of f, if the condition c is true, and otherwise returns the result of g.
 */
export function cond<Y, X> (c: Exp<boolean, X>, f: Exp<Y, X>, g: Exp<Y, X>): Fun<Y, X> {
  return exp(x => val(c, x) ? f : g)
}

/**
 * @remarks
 * Same as {@see cond}, but specialized to sequences and the function g is optional
 */
export function when<X> (c: Exp<boolean, X>, f: Exp<Seq<X>, X>, g: Exp<Seq<X>, X> = noop): Fun<X[], X> {
  return seq(cond(c, f, g))
}

/**
 * @remarks
 * Same as {@see when}, but the condition c is inverted
 */
export function unless<X> (c: Exp<boolean, X>, f: Exp<Seq<X>, X>, g: Exp<Seq<X>, X> = noop): Fun<X[], X> {
  return seq(cond(x => !val(c, x), f, g))
}

/**
 * @returns a function that returns the results of n invocations of the indexer function f
 */
export function range<Y, X> (n: Exp<number, X>, f: (i: number, n: number) => Exp<Y, X>): Fun<Y[], X> {
  return use(n, n => x => {
    return new Array(n).fill(0).map((_, i) => (
      val(f(i, n), x)
    ))
  })
}

/**
 * @returns a function that returns n copies of an argument x
 */
export function clone<X> (n: Exp<number, X>): Fun<X[], X> {
  return range(n, () => x => x)
}

/**
 * @returns a function that returns the results of the mapping function f applied to each element of the array ys, together with an index i, and the full array ys
 */
export function map<Z, Y, X> (ys: Exp<Y[], X>, f: (y: Y, i: number, ys: Y[]) => Exp<Z, X>): Fun<Z[], X> {
  return use(ys, ys => fork(...ys.map(f)))
}

/**
 * @returns a function that returns the items ys sorted by the comparison function f
 */
export function sort<Y, X> (ys: Exp<Y[], X>, f: (y1: Y, y2: Y) => Exp<number, X>): Fun<Y[], X> {
  return use(ys, ys => x => ys.sort((a, b) => val(f(a, b), x)))
}

/**
 * @returns a function that returns the first element of ys, if ys is not empty, or y0 otherwise
 */
export function first<Y, X> (ys: Exp<Y[], X>, y0: Exp<Y, X>): Fun<Y, X> {
  return use(ys, ys => ys.length > 0 ? ys[0] : y0)
}

/**
 * @returns a function that returns exactly those elements from ys that match the condition c
 */
export function filter<Y, X> (ys: Exp<Y[], X>, c: (yi: Y) => Exp<boolean, X>): Fun<Y[], X> {
  return use(ys, ys => x => ys.filter(yi => val(c(yi), x)))
}

/**
 * @returns a function that evaluates the expression returned by f on each element in the array ys, together with an index i, and the full array ys
 *
 * @remarks
 * This is similar to {@see map}, but notice that the expressions are evaluated on each element of ys, rather than on x.
 */
export function indexed<Z, Y, X> (ys: Exp<Y[], X>, f: (i: number, ys: Y[]) => Exp<Z, Y>): Fun<Z[], X> {
  return map(ys, (yi, i, ys) => val(f(i, ys), yi))
}

/**
 * @returns a successful result with the value y
 */
export function pass<Y> (y: Y): Res<Y> {
  return {
    success: true,
    weight: 1,
    get: () => y
  }
}

/**
 * @returns a failing result that raises the error e
 */
export function fail (e?: string | Error): Res<any> {
  return {
    success: false,
    weight: 0,
    get () {
      if (e === undefined) {
        e = 'fail: unspecified failure'
      }
      if (typeof e === 'string') {
        e = new Error(e)
      }
      throw e
    }
  }
}

/**
 * @returns a function that returns the result of f with the weight w
 */
export function weight<Y, X> (w: Exp<number, X>, f: Opt<Y, X>): Fun<Res<Y>, X> {
  return use(w, w => x => (
    {...res(f, x), weight: w}
  ))
}

/**
 * @returns a function that returns the value of the optional expression f, if f succeeds, otherwise returns the value of f0
 */
export function fallback<Y, X> (f0: Exp<Y, X>, f: Opt<Y, X>): Fun<Y, X> {
  return x => {
    const r = res(f, x)
    if (r.success) return r.get()
    return val(f0, x)
  }
}

/**
 * @returns a function that concatenates the sequences generated by the expressions fs
 *
 * @remarks
 * This function takes optional expressions. When one of the expressions fails, it behaves as if the value was an empty sequence.
 */
export function fork<Y, X> (...fs: Opt<Seq<Y>, X>[]): Fun<Y[], X> {
  return x => fs
    .map(f => res<Seq<Y>, X>(f, x))
    .filter(({success}) => success)
    .reduce((a: Y[], {get}) => [...a, ...seq(get())(x)], [])
}

/**
 * @returns a function that is composed of the functions fs
 *
 * @remarks
 * Same as {@see compose}, but for n functions and weaker typing.
 */
export function chain<X> (...fs: Exp<X, X>[]): Fun<X, X> {
  return exp(x => first(pipe(...fs), x))
}

/**
 * @returns a function that composes the functions f and g
 *
 * @remarks
 * Same as {@see chain}, but for two functions and strong typing.
 */
export function compose<Z, Y, X> (f: Exp<Y, X>, g: Exp<Z, Y>): Fun<Z, X> {
  return x => val(g, val(f, x))
}

/**
 * @returns a function that performs context-free derivation with a sequence of rules fs
 *
 * @remarks
 * This function takes optional expressions. When one of the expressions fails, the remaining ones are not applied.
 *
 * The behavior of this function is similar to an L-system.
 */
export function pipe<X> (...fs: Opt<Seq<X>, X>[]): Fun<X[], X> {
  return x => {
    if (fs.length === 0) return [x]
    const [f, ...gs] = fs
    const r: Res<Seq<X>> = res(f, x)
    if (!r.success) return [x]
    const xs: X[] = seq(r.get())(x)
    const recurse = xs.map(exp(pipe(...gs)))
    return val(seq(recurse), x)
  }
}

/**
 * @remarks
 * Same as {@see pipe}, but the passes fs are repeated n times inside another pipe.
 */
export function pipeDeep<X> (n: Exp<number, X>, ...fs: Opt<Seq<X>, X>[]): Fun<X[], X> {
  return use(n, n => (
    pipe(...(n > 0 ? [...fs, pipeDeep(n - 1, ...fs)] : []))
  ))
}

/**
 * @returns a function that passes the value of y to f and returns its result
 */
export function use<Z, Y, X> (y: Exp<Y, X>, f: (context: Y) => Exp<Z, X>): Fun<Z, X> {
  return exp(x => f(val(y, x)))
}

/**
 * @returns a function that is the same as the unary function f, but takes an expression as its argument
 */
export function unary<Y, A> (f: (t: A) => Y) {
  return <X> (a: Exp<A, X>): Fun<Y, X> => {
    return x => f(val(a, x))
  }
}

/**
 * @remarks
 * Same as {@see unary} but with binary functions
 */
export function binary<Y, A, B> (f: (a: A, b: B) => Y) {
  return <X> (a: Exp<A, X>, b: Exp<B, X>): Fun<Y, X> => {
    return x => f(val(a, x), val(b, x))
  }
}

/**
 * @remarks
 * Same as {@see unary} but with ternary functions
 */
export function ternary<Y, A, B, C> (f: (a: A, b: B, c: C) => Y) {
  return <X> (a: Exp<A, X>, b: Exp<B, X>, c: Exp<C, X>): Fun<Y, X> => {
    return x => f(val(a, x), val(b, x), val(c, x))
  }
}
