import {Fragment, h, VNode} from 'preact'
import {InputLabel} from './components/InputLabel'
import {InputField} from './components/InputField'
import {SelectField} from './components/SelectField'
import {Fieldset} from './components/Fieldset'
import {Exp, val} from '@dipl/lib-core'
import {InputButton} from './components/InputButton'
import {InputDivider} from './components/InputDivider'
import {InputCheckbox} from './components/InputCheckbox'

export type Input<T> = (value: T, setValue: (value: T) => void) => VNode

export const toLabel = (key: PropertyKey) => {
  // "camelCase" to "Title Case"
  return String(key)
    .replace(/([a-z])([A-Z])/g, '$1 $2')
    .split(' ')
    .map(x => x[0].toUpperCase() + x.substr(1))
    .join(' ')
}

export function inputs<T> (...inputs: Input<T>[]): Input<T> {
  return (value, setValue) => (
    <Fragment>
      {inputs.map(input => input(value, setValue))}
    </Fragment>
  )
}

export function fieldset<T> (legend: PropertyKey, ...ins: Input<T>[]): Input<T> {
  return (value, setValue) => {
    return (
      <Fieldset legend={toLabel(String(legend))}>
        {inputs(...ins)(value, setValue)}
      </Fieldset>
    )
  }
}

export function label<T> (label: PropertyKey, input: Input<T>): Input<T> {
  return (value, setValue) => (
    <InputLabel label={toLabel(label)}>
      {input(value, setValue)}
    </InputLabel>
  )
}

export function prop<T, K extends keyof T> (key: K, input: Input<T[K]>): Input<T> {
  return (value, setValue) => input(
    value[key],
    value1 => setValue({...value, [key]: value1})
  )
}

export function propKey<T, K extends keyof T> (
  wrap: (key: K, input: Input<T[K]>) => Input<T[K]>,
  key: K,
  input: Input<T[K]>
): Input<T> {
  return prop(key, wrap(key, input))
}

export function item<T> (key: number, input: Input<T>): Input<T[]> {
  return (value, setValue) => input(
    value[key],
    item => setValue(value.map((oldItem, i) => i === key ? item : oldItem))
  )
}

export function preset<T> (key: PropertyKey, preset: Exp<T, T>): Input<T> {
  return (value, setValue) => (
    <InputButton
      rounded={true}
      onClick={() => {
        const newValue = val(preset, value)
        setValue(newValue)
      }}>
      {toLabel(key)}
    </InputButton>
  )
}

export function presets<T> (presets: Record<PropertyKey, Exp<T, T>>): Input<T> {
  return inputs(
    ...Object.keys(presets).map(
      (key): Input<T> => preset(key, presets[key])
    )
  )
}

export function items<T> (map: (item: T, index: number, items: T[]) => Input<T>): Input<T[]> {
  return (value, setValue) => {
    return inputs<T[]>(
      ...value.map((x: T, i) => item<T>(i, map(x, i, value)))
    )(value, setValue)
  }
}

export function object<T> (obj: { [k in keyof T]?: Input<T[k]> }): Input<T> {
  const renderProp = <K extends keyof T> (key: K): Input<T> => {
    const param = obj[key] as Input<T[K]>
    return label(key, prop(key, param))
  }
  const keys = Object.keys(obj) as (keyof T)[]
  return inputs<T>(...keys.map(renderProp))
}

export function slider (min: number = 0, max: number = 1, ticks?: number[]): Input<number> {
  return field({
    type: 'range',
    min,
    max,
    ticks,
    parse: x => Number(x),
    stringify: x => String(x)
  })
}

export function ticks (min: number = 0, max: number = 1, step: number = 1) {
  const nums: number[] = []
  for (let n = min; n <= max; n += step) nums.push(n)
  return slider(min, max, nums)
}

export function number (min?: number, max?: number, step?: number): Input<number> {
  return field({
    type: 'number',
    min,
    max,
    step,
    parse: x => Number(x),
    stringify: x => String(x)
  })
}

export function toggle (): Input<boolean> {
  return (value, setValue) => {
    return <InputCheckbox {...{
      value,
      setValue
    }}/>
  }
}

export function text (minLength: number = 0): Input<string> {
  return field({
    parse: x => x,
    stringify: x => x,
    minLength
  })
}

export function field<T> (props: {
  type?: string
  step?: number
  min?: number
  max?: number
  minLength?: number
  ticks?: number[]
  parse: (str: string) => T
  stringify: (value: T) => string
}): Input<T> {
  return (value, setValue) => {
    return <InputField {...{
      value,
      setValue,
      ...props
    }}/>
  }
}

export function names<T extends PropertyKey> (...items: T[]) {
  return select({
    items,
    stringify: x => String(x),
    parse: x => x as T
  })
}

export function select<T> (props: {
  items: T[]
  parse: (str: string) => T
  stringify: (value: T) => string
}): Input<T> {
  return (value, setValue) => {
    return <SelectField {...{
      value,
      setValue,
      ...props
    }}/>
  }
}

export function divider (): Input<any> {
  return () => <InputDivider/>
}